}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  scroll-behavior: smooth;
}
.page-body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
  }
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.link {
  color: #000000;
  transition: color 0.3s;
}
.text {
  margin: 0;
}
.title {
  --width-block: 320px;
  --padding: 30px;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
}
.title--the-main-for-the-other-pages {
  width: calc(var(--width-block) - (var(--padding) * 2));
  text-align: center;
  font-size: 27px;
}
@media (min-width: 768px) {
  .title {
    --width-block: 768px;
    --padding: 50px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    --width-block: 1150px;
    font-size: 53px;
    line-height: 57px;
  }
}
.wrapper-title {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 175px;
  padding-top: 73px;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .wrapper-title {
    min-height: 160px;
    padding-top: 35px;
    padding-bottom: 45px;
  }
  .wrapper-title::after {
    content: "";
    position: absolute;
    bottom: -5px;
    width: 100%;
    height: 20px;
    background-image: url("../img/zigzag-fill-desktop.svg");
    background-repeat: repeat-x;
    background-size: contain;
  }
}
@media (min-width: 1150px) {
  .wrapper-title {
    min-height: 200px;
  }
  .wrapper-title::after {
    height: 34px;
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 260px;
  min-height: 52px;
  padding: 7px;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
  border: 2px solid #444444;
  transition-property: color, background-color;
  transition-duration: 0.4s;
  transition-timing-function: ease-in;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.button:hover,
.button:focus {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.form-fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
.blockquote {
  margin: 0;
}
.up-button {
  position: fixed;
  z-index: 1;
  display: none;
  padding: 0;
  bottom: 175px;
  right: 30px;
  width: 64px;
  height: 64px;
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  color: #63d1bb;
  background-color: rgba(0, 0, 0, 0);
  border: 5px solid #63d1bb;
  border-radius: 50%;
  cursor: pointer;
}
.shown {
  display: inline-block;
}
@media (min-width: 768px) {
  .page-header {
    background-image: linear-gradient(#ffffff 88px, #ececec 88px, #ececec 89px, #f9f9f9 89px);
  }
}
@media (min-width: 768px) {
  .page-header--dark {
    background-image: linear-gradient(#ffffff 88px, #ececec 88px, #ececec 89px, #f1f1f1 89px);
  }
}
.main-nav {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    justify-content: space-between;
    width: 1150px;
    margin: 0 auto;
  }
}
.main-nav__logo {
  display: flex;
  width: 90px;
  height: min-content;
  padding: 22px 0;
  margin: 0 auto;
  transition: opacity 0.3s;
}
.main-nav__logo[href]:hover,
.main-nav__logo[href]:focus {
  opacity: 0.6;
}
.main-nav__logo[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__logo {
    width: 115px;
    margin: 0;
    padding: 0;
    padding-top: 22px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    width: 150px;
    padding-top: 9px;
    order: 2;
  }
}
.main-nav__logo-img {
  width: 86px;
  height: 35px;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .main-nav__logo-img {
    width: 114px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-img {
    width: 149px;
    height: 72px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 18px;
  right: 22px;
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 10px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.3), 0 12px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 5px;
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__item {
  display: flex;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .main-nav__list--site {
    order: 2;
  }
}
.main-nav__list--site > .main-nav__item:last-child {
  border: none;
}
@media (min-width: 768px) {
  .main-nav__list--site > .main-nav__item {
    width: 167px;
    padding-left: 0;
    padding-right: 20px;
  }
}
@media (min-width: 1150px) {
  .main-nav__list--site {
    max-width: 405px;
    order: 1;
  }
  .main-nav__list--site > .main-nav__item {
    width: 184px;
  }
  .main-nav__list--site > .main-nav__item:first-of-type {
    border-right: 1px solid #ececec;
    margin-right: 35px;
  }
}
@media (min-width: 768px) {
  .main-nav__list--user {
    display: flex;
    flex-basis: 75%;
    justify-content: flex-end;
    margin-left: auto;
  }
}
@media (min-width: 1150px) {
  .main-nav__list--user {
    flex-basis: auto;
    max-width: 360px;
    margin-left: 51px;
    order: 3;
  }
}
.main-nav__list--sale {
  display: none;
}
@media (min-width: 1150px) {
  .main-nav__list--sale {
    display: flex;
    order: 4;
  }
  .main-nav__list--sale > .main-nav__item {
    width: 220px;
    padding-right: 20px;
    padding-left: 0;
    padding-top: 17px;
  }
}
.main-nav__item {
  position: relative;
  display: flex;
  min-height: 79px;
  height: fit-content;
  padding-top: 28px;
  padding-left: 75px;
  border-bottom: 1px solid #ececec;
}
.main-nav__item:first-child {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__item {
    min-height: 89px;
    padding-top: 33px;
    border: none;
    padding-bottom: 10px;
  }
  .main-nav__item--site {
    min-height: 65px;
    padding-top: 21px;
  }
  .main-nav__item--cart {
    width: 270px;
  }
  .main-nav__item:first-child {
    border-top: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__item--site {
    min-height: 89px;
    padding-top: 33px;
  }
  .main-nav__item--sale {
    min-height: 65px;
  }
}
.main-nav__item--search {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__item--search {
    width: 82px;
    padding-left: 10px;
    padding-right: 10px;
    border: none;
    border-right: 1px solid #ececec;
    border-left: 1px solid #ececec;
  }
}
@media (min-width: 768px) {
  .main-nav__item--cart {
    padding-left: 85px;
  }
}
.main-nav__link {
  position: relative;
  font-weight: bold;
  font-size: 17px;
}
.main-nav__link svg {
  position: absolute;
  opacity: 0.2;
  transition-property: color, opacity;
  transition-timing-function: ease;
  transition-duration: 0.3s;
}
.main-nav__link[href]:hover,
.main-nav__link[href]:focus {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__link:hover svg,
.main-nav__link:focus svg {
  color: #63d1bb;
  opacity: 1;
}
.main-nav__link:active svg {
  color: #63d1bb;
}
@media (min-width: 768px) {
  .main-nav__link--site {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .main-nav__link--site {
    font-size: 17px;
  }
}
@media (min-width: 1150px) {
  .main-nav__link--sale {
    font-size: 15px;
  }
}
.main-nav__link--search svg {
  top: 0;
  left: -45px;
}
.main-nav__link--search::before {
  content: attr(aria-label);
}
@media (min-width: 768px) {
  .main-nav__link--search::before {
    content: "";
  }
  .main-nav__link--search svg {
    top: 4px;
    left: 23px;
  }
}
@media (min-width: 1150px) {
  .main-nav__link--search svg {
    left: 22px;
  }
}
.main-nav__link--cart svg {
  left: -45px;
  top: 0;
}
@media (min-width: 768px) {
  .main-nav__link--cart svg {
    left: -52px;
    top: 3px;
  }
}
.main-nav__text {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__text {
    display: flex;
    align-items: center;
    order: 5;
    margin-left: auto;
    padding-left: 20px;
    color: #444444;
  }
}
.content {
  position: relative;
  background-color: #f9f9f9;
  background-image: url("../img/index-page/hero-mobile.jpg");
  background-repeat: no-repeat;
  background-position: 51% -20px;
}
@media (min-resolution: 2dppx) {
  .content {
    background-image: url("../img/index-page/hero-mobile@2x.jpg");
    background-size: 330px 375px;
  }
}
@media (min-width: 768px) {
  .content {
    background-image: url("../img/index-page/hero-desktop.jpg");
    background-position-y: -67px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .content {
    background-image: url("../img/index-page/hero-desktop@2x.jpg");
    background-size: 1150px 745px;
  }
}
@media (min-width: 768px) {
  .content::after {
    content: "";
    position: absolute;
    top: 665px;
    width: 100%;
    height: 20px;
    background-image: url("../img/zigzag-fill-desktop.svg");
    background-size: contain;
  }
}
@media (min-width: 1150px) {
  .content::after {
    height: 34px;
  }
}
.content__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 490px;
  max-width: 1150px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .content__wrapper {
    min-height: 683px;
  }
}
@media (min-width: 1150px) {
  .content__wrapper {
    min-height: 695px;
  }
}
.content__title {
  padding: 73px 30px 20px 30px;
  font-weight: bold;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
}
@media (min-width: 768px) {
  .content__title {
    padding: 90px 66px 20px 50px;
    font-size: 43px;
    line-height: 50px;
  }
  .content__title--mobile {
    display: none;
  }
}
@media (min-width: 1150px) {
  .content__title {
    padding-right: 53px;
    font-size: 53px;
    line-height: 57px;
  }
}
.content__inner {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: auto;
}
@media (min-width: 768px) {
  .content__inner {
    flex-direction: row;
    padding: 0 50px;
  }
}
.content__link {
  --height-content-link: 103px;
  position: relative;
  display: flex;
  justify-content: space-between;
  min-width: 50%;
  min-height: var(--height-content-link);
  padding: 22px 100px 10px 29px;
  font-weight: bold;
  font-size: 19px;
  color: #ffffff;
  background-color: #63d1bb;
}
.content__link:nth-child(2) {
  background-color: #56c3b2;
}
.content__link svg {
  position: absolute;
  right: 35px;
  top: 30px;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .content__link {
    --height-content-link: 123px;
    max-width: 50%;
    padding-left: 32px;
    padding-top: 26px;
    padding-bottom: 30px;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .content__link {
    --height-content-link: 134px;
    padding-left: 37px;
  }
}
