  transform: rotate(45deg);
}
.checkbox-list__item input:checked:disabled + label::after {
  border-bottom: 2px solid rgba(0, 0, 0, 0.3);
  border-right: 2px solid rgba(0, 0, 0, 0.3);
  cursor: default;
}
@media (min-width: 768px) {
  .checkbox-list__item input + label::before {
    top: 1px;
    left: 3px;
  }
  .checkbox-list__item input:checked + label::after {
    top: 7px;
    left: 20px;
    width: 16px;
    height: 29px;
    border-bottom: 2px solid #000000;
    border-right: 2px solid #000000;
    transform: rotate(45deg);
  }
}
@media (min-width: 1150px) {
  .checkbox-list__item input + label::before {
    top: 0;
    left: -4px;
  }
  .checkbox-list__item input:checked + label::after {
    top: 5px;
    left: 14px;
  }
}
.page-main {
  flex-grow: 1;
}
.page-main--blank-page {
  margin: 0 auto;
}
.page-main--blank-page svg {
  width: 260px;
  height: auto;
}
@media (min-width: 768px) {
  .page-main--blank-page svg {
    width: 540px;
  }
}
.page-main__404 {
  transform-origin: 85px 4px;
  animation: swing 12s 0.5s infinite ease-out;
}
@keyframes swing {
  0% {
    transform: rotate(0);
  }
  5% {
    transform: rotate(3deg);
  }
  15% {
    transform: rotate(-2.5deg);
  }
  25% {
    transform: rotate(2deg);
  }
  35% {
    transform: rotate(-1.5deg);
  }
  45% {
    transform: rotate(1deg);
  }
  55% {
    transform: rotate(-1.5deg);
  }
  65% {
    transform: rotate(2deg);
  }
  75% {
    transform: rotate(-2deg);
  }
  85% {
    transform: rotate(2.5deg);
  }
  95% {
    transform: rotate(-3deg);
  }
  100% {
    transform: rotate(0);
  }
}
